<template>
  <div>
    <van-nav-bar
      title="反馈"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-field
        v-model="text"
        type="textarea"
        name="反馈内容"
        label="反馈内容"
        rows="3"
        autosize
        placeholder="请输入反馈内容"
        :rules="[{ required: true}]"
      />
      <van-divider />
      <div style="margin: 16px;">
        <van-button color="#FF6666" round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "feedback",
  data(){
    return{
      text:""
    }
  },
  methods:{
    onClickLeft(){
      this.$router.go(-1)
    },
    onSubmit(values) {
      axios.post("student/submitFeedback",this.text).then(res=>{
        this.$dialog.alert({
          message: res.data.message,
        }).then(() => {
          this.onClickLeft()
        });
      })
      console.log(this.text)
    }
  }
}
</script>

<style scoped>

</style>



