<template>
  <div>
    <van-nav-bar
      title="活动详情"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-image
      width="100%"
      height="38.2vh"
      src="https://img01.yzcdn.cn/vant/cat.jpeg"
    />
    <van-divider />
    <van-form @submit="onSubmit">
      <van-field
        readonly
        name="活动名"
        label="活动名"
        :placeholder="detail.name"
      />
      <van-field
        readonly
        name="开始时间"
        label="开始时间"
        :placeholder="detail.startTime"
      />
      <van-field
        readonly
        name="结束时间"
        label="结束时间"
        :placeholder="detail.endTime"
      />
      <van-field
        readonly
        name="活动地点"
        label="活动地点"
        :placeholder="detail.location"
      />
      <van-row>
        <van-col span="12">
          <van-field
            readonly
            name="活动人数"
            label="活动人数"
            :placeholder="detail.volunteerNum"
          />
        </van-col>
        <van-col span="12">
          <van-field
            readonly
            name="活动时长"
            label="活动时长"
            :placeholder="detail.hours"
          />
        </van-col>
      </van-row>
      <van-field
        v-model="message"
        readonly
        autosize
        label="活动详情"
        type="textarea"
        :placeholder="detail.summary"
      />
    </van-form>
    <van-goods-action>
      <van-goods-action-button
        color="#FF6666"
        text="报名"
        @click="signUp"
      />
    </van-goods-action>
  </div>
</template>

<script>
import ActivityDetail from "../components/ActivityDetail";
import {Dialog, Toast} from "vant";
import axios from "axios";
export default {
  name: "detail",
  components: {ActivityDetail},
  data(){
    return{
      detail:{}
    }
  },
  mounted() {
    this.detail=this.$route.query.activity
  },
  methods:{
    onClickLeft(){
      this.$router.go(-1)
    },
    signUp(){
      var that=this;
      Dialog.confirm({
        title: '是否确认报名？',
        theme: 'round-button',
        confirmButtonColor:"#FF6666",
        cancelButtonColor:"#FFCCCC"
      })
        .then(() => {
          Toast.success('申请报名成功，待报名结束后关注已参与的活动噢~');

          axios.post("student/signUp",that.detail.id).then(res=>{
            this.$dialog.alert({
              message: res.data.message,
            }).then(() => {
              this.onClickLeft()
            });
          })
        })
        .catch(() => {
          // on cancel
        });
    }
  }
}
</script>

<style >
.van-nav-bar .van-icon {
  color:#FF6666;
}
</style>



