<template>
  <div>
    <van-popup inset v-model="show" position="top" :style="{ height: auto }">
      <van-cell title="反馈" is-link to="/feedback" />
      <van-cell title="修改密码" is-link to="/changePwd"/>
    </van-popup>
    <van-nav-bar title="志愿易">
      <template #right>
        <img src="../assets/icon/说事规则.svg" height="18" width="18" @click="showPopOver"/>
      </template>
    </van-nav-bar>
    <van-divider/>
    <van-row>
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item>1</van-swipe-item>
        <van-swipe-item>2</van-swipe-item>
        <van-swipe-item>3</van-swipe-item>
        <van-swipe-item>4</van-swipe-item>
      </van-swipe>
    </van-row>
    <van-divider/>
    <van-row>
      <MyData></MyData>
    </van-row>

    <Tabbar></Tabbar>
  </div>
</template>

<script>
import Tabbar from '../components/Tabbar';
import MyData from '../components/MyData'
import IndexSwipe from "../components/IndexSwipe";
import axios from "axios";
export default {
  name: "index",
  data() {
    return {
      active:1,
      show: false,
      Popover: false,
      // 通过 actions 属性来定义菜单选项
      actions: [{ text: '选项一' }, { text: '选项二' }, { text: '选项三' }],
    }
  },
  methods: {
    showPopOver(){
      this.show=true
      console.log(this.Popover)
    },
    onSelect(action) {
      Toast(action.text);
    },
  },
  components: {IndexSwipe, Tabbar, MyData},

}
</script>
<style scoped>
  .my-swipe .van-swipe-item {
    color: #fff;
    font-size: 20px;
    line-height: 150px;
    text-align: center;
    background-color: #39a9ed;
  }
  .wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
  }

  .block {
    width: 120px;
    height: 120px;
    background-color: #fff;
  }
</style>



