<template>
  <div>
    <router-view/>
    <van-nav-bar
      title="我的活动"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-dropdown-menu active-color="#99ccff">
      <van-dropdown-item v-model="value1" :options="option1" />
    </van-dropdown-menu>
    <van-card
      desc="描述信息"
      title="商品标题"
      thumb="https://img01.yzcdn.cn/vant/ipad.jpeg"
    >
      <template #tags>
        <van-tag plain type="danger">标签</van-tag>
        <van-tag plain type="danger">标签</van-tag>
      </template>
    </van-card>
  </div>
</template>

<script>
export default {
  name: "myActivity",
  data(){
    return{
      value1: 0,
      option1: [
        { text: '全部活动', value: 0 },
        { text: '未开始', value: 1 },
        { text: '已结束', value: 2 },
      ],
    }
  },
  methods: {
    onClickLeft(){
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>

</style>



