<template>
  <div id="contains">
    <div class="login-body">
      <van-form @submit="onSubmit" style="width: 80%;margin-left: 10%">
        <van-field
          v-model="username"
          name="username"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="password"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <div style="margin: 16px;">
          <van-button color="#FF6666" round block type="info" native-type="submit">登录</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>

import axios from "axios";

export default {
  name: "register",
  data(){
    return{
      username:'',
      password:'',
    }
  },
  methods:{
    onSubmit(values) {
      var str="studentNo="+values.username+"&password="+values.password
      // username=01&password=123456
      console.log(str)
      axios.post('login',str).then(res=>{
        console.log(res)
        if(res.data.status=="success"){
          this.$router.push('/index')
        }
      })
    },
  },
  components: {}
}
</script>

<style scoped>
#contains{
  height: 100vh;
  background-color: white;
}
.login-body{
  background-color: #FFCCCC;
  width: 80%;
  margin-left: 10%;
  margin-top: 5vh;
  height: 50vh;
  padding-top: 20vh;
}
VanField{

}
</style>



