// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import './assets/rem'
import Vue from 'vue'
import axios from "axios";
import App from './App'
import router from './router'
import './assets/icon/iconfont.css'
import{Dialog,Toast,Popover,Popup,Icon,Tabbar,TabbarItem,Image as VanImage,GoodsAction,GoodsActionButton,SubmitBar,Tag,Divider,Cell,ContactCard,Button,NavBar,Row,Col,CellGroup,Field,Form,Swipe,SwipeItem,Overlay,Grid,GridItem,Card,DropdownMenu,DropdownItem} from "vant";
Vue.use(Dialog).use(Toast).use(Popover).use(Popup).use(Icon).use(Tabbar).use(TabbarItem).use(VanImage).use(GoodsActionButton).use(GoodsAction).use(SubmitBar).use(Divider).use(Button).use(NavBar).use(Row).use(Col).use(CellGroup).use(Field).use(Form).use(Swipe).use(SwipeItem).use(Overlay).use(Grid).use(GridItem).use(Card).use(DropdownItem).use(DropdownMenu).use(ContactCard).use(Cell).use(Tag)
Vue.config.productionTip = false
Vue.prototype.$axois=axios
axios.defaults.baseURL='/api'

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



