<template>
  <div class="van-hairline--surround band">
    {{name}}，你好：<br>&nbsp;&nbsp;&nbsp;&nbsp;你已参加&nbsp;
    <a class="activeNum">{{activityNum}}</a>
    &nbsp;项志愿活动，已有&nbsp;
    <a class="activeNum">{{activityHour}}</a>
    &nbsp;小时志愿时长！<br>&nbsp;&nbsp;&nbsp;&nbsp;——计算机学院“奔腾”青年志愿者协会
  </div>

</template>

<script>
import axios from "axios";

export default {
  name: "MyData",
  data(){
    return{
      name:"xxx",
      activityNum:12,
      activityHour:220
    }
  },
  mounted() {
    var that=this;
    axios("student/activitySummary").then(res=>{
      that.name=res.data[0]
      that.activityNum=res.data[1]
      that.activityHour=res.data[2]
    })
  },
}
</script>

<style scoped>
.band{
  font-family: "Arial","Microsoft YaHei","黑体","宋体",sans-serif;
  padding: 10px;
  margin: 12px;
  border: 10px solid #FF9966;
  font-size: 6vmin;
  background-color: #ffcccc;
  border-radius: 10px;
  line-height:12vmin
}
.activeNum{
  color: #ff6666;
}
</style>



