<template>
  <div>
    <van-nav-bar
      title="修改密码"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-form @submit="onSubmit">
      <van-field
        v-model="originpwd"
        name="身份证号"
        label="身份证号"
        placeholder="请输入身份证号"
        :rules="[{ required: true}]"
      />
      <van-field
        v-model="password1"
        type="password"
        name="新密码"
        label="新密码"
        placeholder="请输入新密码"
        :rules="[{ required: true}]"
      />
      <van-field
        v-model="password2"
        type="password"
        name="确认密码"
        label="确认密码"
        placeholder="请确认新密码"
        :rules="[{ required: true}]"
      />
      <van-divider />
      <div style="margin: 16px;">
        <van-button color="#FF6666" round block type="info" native-type="submit">提交</van-button>
      </div>
    </van-form>
  </div>
</template>

<script>
import {Toast} from "vant";
import axios from "axios";

export default {
  name: "changePwd",
  data(){
    return{
      originpwd:"",
      password1:"",
      password2:""
    }
  },
  methods: {
    onClickLeft(){
      this.$router.go(-1)
    },
    onSubmit(){
      if (this.password1!=this.password2) {
        Toast.fail('两次输入密码不相同');
        return;
      }
      var that=this;
      axios.post("student/applyRevisePwd",this.originpwd).then(res=>{
        if (res.data.status=="failed"){
          Toast.fail(res.data.message);
        }else {
          axios.post("user/revisePwd",this.password1).then(rres=>{
            Toast.success(rres.data.message);
            if (rres.data.status=="success"){
              that.onClickLeft();
            }
          })
        }
      })
    }
  }
}
</script>

<style scoped>

</style>



