<template>
  <div>
    <van-nav-bar
      title="活动"
    />
    <van-dropdown-menu active-color="#FF6666">
      <van-dropdown-item @change="select" v-model="value1" :options="option1" />
    </van-dropdown-menu>
    <van-card v-for="(item,i) in preactivity" :key="i"
      currency=""
      :desc="item.summary"
      :title="item.name"
      thumb="https://img01.yzcdn.cn/vant/ipad.jpeg"
    >
      <template #tags>
        <van-tag v-if="item.statusId==1" color="#FFCCCC" text-color="#FF6666">开始</van-tag>
        <van-tag v-if="item.statusId==2"color="#FF6666" plain>未开始</van-tag>
      </template>
      <template #footer>
        <van-button @click="detail(item.id)" color="#FF6666" size="small">详情</van-button>
      </template>
    </van-card>
    <Tabbar></Tabbar>
  </div>
</template>

<script>
import DropdownMine from "../components/DropdownMine";
import Tabbar from "../components/Tabbar";
import axios from "axios";
export default {
  name: "activityList",
  components: { Tabbar, DropdownMine },
  data(){
    return{
      activity:[],
      preactivity:[],
      value1:0,
      option1: [
        { text: '全部活动', value: 0 },
        { text: '未开始报名', value: 1 },
        { text: '已开始报名', value: 2 },
      ],
    }
  },
  methods:{
    select:function (e) {
      if (e==0){
        this.preactivity=this.activity;
      }else if(e==1){
        for (const item in this.activity) {
          // if (item.)
        }
        this.preactivity
      }
    },
    detail:function (e) {
      this.$router.push({
        path:'/detail',
        query:{
          activity:this.activity[e-1]
        }
      })
    }
  },
  mounted() {
    axios("admin/loadAllActivity").then(res=>{
      this.activity=res.data
      this.preactivity=res.data
    })
  }
};
</script>

<style scoped></style>



