<template>
  <div>
    <van-nav-bar title="我的"/>
    <van-dropdown-menu>
      <van-dropdown-item v-model="value1" :options="option1" />
    </van-dropdown-menu>
    <van-card v-for="(item,i) in activity" :key="i"
      currency=""
      :desc="item.summary"
      :title="item.name"
      thumb="https://img01.yzcdn.cn/vant/ipad.jpeg"
    >
      <template #tags>
        <van-tag v-if="item.statusId==1" color="#FFCCCC" text-color="#FF6666">成功</van-tag>
        <van-tag v-if="item.statusId==2" color="#FF6666" plain>结束</van-tag>
        <van-tag v-if="item.statusId==3" color="#FFCCCC" plain>报名中</van-tag>
      </template>
      <template #footer>
        <van-button @click="cancel(i)" v-if="item.statusId==1" color="#FFCCCC" size="small">取消</van-button>
        <van-button @click="myDetail(i)" color="#FF6666" size="small">详情</van-button>
      </template>
    </van-card>
    <Tabbar></Tabbar>
  </div>
</template>

<script>
import Tabbar from '../components/Tabbar.vue';
import axios from "axios";
import {Toast} from "vant";
export default {
  name: "mine",
  components: {Tabbar},
  inject:['reload'],
  data(){
  return{
    activity:[],
    value1: 0,
    option1: [
      { text: '全部活动', value: 0 },
      { text: '报名失败', value: 1 },
      { text: '报名成功', value: 2 },
      { text: '已结束', value: 3 }
    ]
  }
},
  methods:{
    cancel:function (e) {
      Toast.success('取消报名成功，下次报名前要考虑清楚噢！');
      this.reload();
      axios.post("student/cancelSign",this.activity[e].id).then(res=>{
        console.log(res)
      })
    },
    myDetail:function (e){
      this.$router.push({
        path:'/myDetail',
        query:{
          activity:this.activity[e]
        }
      })
    }
  },
  mounted() {
    axios("student/loadMyActivity").then(res=>{
      this.activity=res.data
    })
  }
}
</script>

<style scoped>

</style>



