<template>
  <div>
    <van-nav-bar
      title="活动详情"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-image
      width="100%"
      height="38.2vh"
      src="https://img01.yzcdn.cn/vant/cat.jpeg"
    />
    <van-divider />
    <van-form @submit="onSubmit">
      <van-field
        readonly
        name="活动名"
        label="活动名"
        :placeholder="detail.name"
      />
      <van-field
        readonly
        name="开始时间"
        label="开始时间"
        :placeholder="detail.startTime"
      />
      <van-field
        readonly
        name="结束时间"
        label="结束时间"
        :placeholder="detail.endTime"
      />
      <van-field
        readonly
        name="活动地点"
        label="活动地点"
        :placeholder="detail.location"
      />
      <van-row>
        <van-col span="12">
          <van-field
            readonly
            name="活动人数"
            label="活动人数"
            :placeholder="detail.volunteerNum"
          />
        </van-col>
        <van-col span="12">
          <van-field
            readonly
            name="活动时长"
            label="活动时长"
            :placeholder="detail.hours"
          />
        </van-col>
      </van-row>
      <van-field
        readonly
        name="联系方式"
        label="联系方式"
        :placeholder="detail.contactWay"
      />
      <van-field
        v-model="message"
        readonly
        autosize
        label="活动详情"
        type="textarea"
        :placeholder="detail.summary"
      />
    </van-form>
  </div>
</template>

<script>
import ActivityDetail from "../components/ActivityDetail";
import {Dialog} from "vant";
import axios from "axios";
export default {
  name: "detail",
  components: {ActivityDetail},
  data(){
    return{
      detail:{}
    }
  },
  mounted() {
    this.detail=this.$route.query.activity
  },
  methods:{
    onClickLeft(){
      this.$router.go(-1)
    }
  }
}
</script>

<style >
.van-nav-bar .van-icon {
  color:#FF6666;
}
</style>



